@mixin rob {
	font-family: 'Roboto', sans-serif; }

$white: #fff;
$black: #000;
$black2: $black;
$orange: #fdb301;
$orange2: #f2ac04;
$orange3: #fdbf01;
$orange4: #fdad01;

body {
	background: $white;
	line-height: 1.2;
	@include rob;
	color: $black2;
	font-size: 18px;
	@include m {
		font-size: 16px; } }
a {
	text-decoration: none; }

h1,h2,h3,h4,h5,h6 {
	font-weight: normal; }
strong {
	font-weight: 700; }
.title-sm {
	color: $white;
	font-size: 24px;
	font-weight: 700;
	text-align: center;
	@include md {
		font-size: 20px; }
	@include sm {
		font-size: 18px; }
	&--orange {
		color: $orange4; }
	&--line {
		display: flex;
		align-items: center;
		justify-content: center;
		span {
			display: block;
			margin: 0 12px; }
		&:before, &:after {
			background: $white;
			content: '';
			height: 2px;
			flex-grow: 555; } } }
.title-l {
	color: $white;
	font-size: 30px;
	font-weight: 700;
	text-align: center;
	margin-bottom: 28px; }
.title-md {
	color: $orange4;
	font-size: 27px;
	font-weight: 700;
	text-align: center;
	@include md {
		font-size: 25px; }
	@include sm {
		font-size: 20px; } }
.title {
	text-align: center;
	font-size: 36px;
	color: $black;
	font-weight: 300;
	margin-bottom: 60px;
	strong {
		color: $orange4; }
	@include md {
		font-size: 26px; }
	@include sm {
		font-size: 22px; }
	&--sm-font {
		font-size: 30px; }
	&--white {
		color: $white; }
	&--decor {
		position: relative;
		margin-bottom: 60px;
		font-weight: 700;
		&:before {
			content: '';
			position: absolute;
			bottom: -10px;
			width: 30px;
			height: 5px;
			background: $orange;
			left: 50%;
			transform: translateX(-50%); } } }


.bg-styles {
	background-position: 50% 50%;
	background-repeat: no-repeat;
	background-size: cover; }

.dot-list {
	margin-bottom: 60px;
	@include md {
		margin-bottom: 40px; }

	li {
		position: relative;
		padding-left: 35px;
		font-size: 30px;
		color: $white;
		margin-bottom: 38px;
		@include r(1200) {
			font-size: 22px;
			margin-bottom: 20px; }
		@include xs {
			font-size: 16px;
			margin-bottom: 10px; }
		&:last-of-type {
			margin-bottom: 0; }
		&:before {
			content: '';
			position: absolute;
			width: 13px;
			height: 13px;
			background-color: $orange3;
			left: 0;
			top: 11px;
			border-radius: 50%;
			@include r(1200) {
				top: 7px; }
			@include xs {
				top: 2px; } } } }
.check-list {
	li {
		padding-left: 62px;
		position: relative;
		font-size: 18px;
		font-weight: 300;
		margin-bottom: 40px;
		@include md {
			padding-left: 26px;
			margin-bottom: 20px;
			font-size: 16px; }
		&:last-of-type {
			margin-bottom: 0; }
		i {
			position: absolute;
			line-height: 1;
			font-size: 0;
			display: block;
			left: 0;
			top: 50%;
			transform: translateY(-50%);
			width: 32px;
			height: 32px;
			background-color: #fdb51a;
			@include md {
				width: 20px;
				height: 20px;
				top: 0px;
				transform: translateY(0%); }
			&:before {
				content: '';
				@include vcenter;
				@include s(check); } } } }
.article-wrapper {
	padding-top: 150px;
	margin-bottom: 50px;
	background-size: cover;

	@include m {
		padding-top: 250px; } }
.imgs-section {
	margin: 50px 0;
	display: flex;
	justify-content: space-around;
	font-size: 0;
	@include md {
		margin: 30px 0; }
	@include sm {
		display: block; }
	&__item {
		position: relative;
		img {
			max-width: 100%;
			height: auto; }
		@include md {
			width: 220px; }
		@include sm {
			width: auto;
			max-width: 300px;
			margin-left: auto;
			margin-right: auto;
			margin-bottom: 35px;
			text-align: center; }
		i {
			width: 50px;
			height: 50px;
			background-color: #fcbf00;
			color: white;
			font-size: 30px;
			font-weight: 700;
			text-align: center;
			display: block;
			font-style: normal;
			line-height: 50px;
			position: absolute;
			border-radius: 50%;
			top: 0;
			left: 0;
			transform: translate(-20%, -30%); } }
	&--4-img {
		margin: 20px 0 0;
		justify-content: space-between;
		flex-shrink: 1;
		.imgs-section__item {
			margin: 0px 15px;
			@include sm {
				margin: 0 auto 20px; }
			&:first-of-type {
				margin-left: 0;
				@include sm {
					margin: 0 auto 20px; } }
			&:last-of-type {
				margin-right: 0;
				@include sm {
					margin: 0 auto; } }
			img {
				max-width: 100%;
				height: auto; } } } }
.article {
	color: #010100;
	font-size: 18px;
	font-weight: 300;
	@include m {
		font-size: 16px; }
	ul {
		li {
			margin-bottom: 17px;
			&:last-of-type {
				margin-bottom: 0; } }
		&.dot-list {
			li {
				color: #393a3d;
				font-size: 18px;
				font-weight: 300;
				padding-left: 24px;
				&:before {
					top: 3px; }
				@include m {
					font-size: 16px; } } } }
	p {
		margin-bottom: 17px;
		@include sm {
			margin-bottom: 10px; }
		&:last-of-type {
			margin-bottom: 0; } }

	&__img {
		float: left;
		margin-right: 40px;
		position: relative;
		top: -40px;

		@include sm {
			float: none;
			display: block;
			margin-left: auto;
			margin-right: auto;
			margin-bottom: 30px;
			top: 0; } }
	h2 {
		color: #393a3d;
		font-size: 24px;
		font-weight: 700;
		margin-bottom: 18px;
		@include md {
			font-size: 22px; }
		@include sm {
			font-size: 20px; } }
	div {
		@include clr;
		@include m {
			margin-bottom: 30px; } }
	&--white {
		color: $white; }
	&--sm-pad {
		ul li, p {
			margin-bottom: 10px;
			&:last-of-type {
				margin-bottom: 0; } } } }
.decor-block {
	padding-left: 15px;
	border-left: 3px solid $orange;
	@include sm {
		padding-left: 0;
		border: none; } }
.video-block {
	border-top: 3px solid $orange4;
	border-bottom: 3px solid $orange4;
	width: 945px;
	height: 535px;
	margin-left: auto;
	margin-right: auto;
	@include r(1150) {
		width: 600px;
		height: 320px; }
	@include sm {
		width: 460px;
		height: 270px; }
	@include xs {
		width: 100%;
		max-width: 400px;
		height: 240px; }
	@include r(400) {
		height: 200px; }
	iframe {
		width: 100%;
		height: 100%; } }
.link-orange {
	color: $orange4;
	font-size: 27px;
	font-weight: 700;
	display: inline-block;
	vertical-align: top; }

.socials {
	display: inline-flex;
	justify-content: space-between;
	font-size: 0;
	a {
		width: 35px;
		height: 35px;

		@include trans;
		position: relative;
		&:hover {
			background-color: $orange;
			svg {
				fill: $white; } } }
	i {
		line-height: 1;
		@include vcenter; }
	svg {
		fill: $black;
		font-size: 20px;
		@include trans; } }
.is-hidden {
	overflow: hidden; }
