.hero {
	padding-top: 128px;
	@include m {
		padding-top: 250px; } }
.hslider {
	padding: 0 33px;
	position: relative;
	@include m {
		padding: 0; }
	&__slide {
		color: $white; }
	&__title {
		font-size: 36px;
		font-weight: 700;
		margin-bottom: 40px;
		text-align: center;
		@include md {
			font-size: 24px;
			margin-bottom: 20px;
			br {
				display: none; } } }
	&__sub-title {
		font-size: 30px;
		margin-bottom: 40px;
		text-align: center;
		@include md {
			font-size: 20px; } }

	&__inner {
		font-size: 0; }

	&__img {
		display: inline-block;
		vertical-align: top;
		margin-right: 35px;
		@include md {
			display: block;
			margin-right: 0;
			text-align: center; }
		img {
			max-width: 480px;
			height: auto;
			display: inline-block;
			@include sm {
				max-width: 100%; } } }

	&__right {
		display: inline-block;
		vertical-align: top;

		@include md {
			display: block;
			max-width: 400px;
			margin-left: auto;
			margin-right: auto;
			text-align: center; }
		.btn-border {
			@include md {
				margin-bottom: 40px; } }
		.dot-list {
			@include md {
				text-align: left; } }
		&--sm {
			@include md {
				.dot-list {
					max-width: 200px;
					margin-left: auto;
					margin-right: auto; } } }

		//
 }		// 	margin-top: 60px
	.slick-arrow {
		top: 40%; } }
.aft-hero-sec {
	padding-top: 60px;
	padding-bottom: 60px;
	@include m {
		padding: 30px 0; }
	.article {
		@include clr;
		img {
			float: right;
			margin-left: 30px; }
		@include sm {
			img {
				float: none;
				display: block;
				margin: 0 auto 30px;
				max-width: 100%; } } } }
.low-price {
	padding-top: 48px;
	padding-bottom: 43px;
	@include md {
		padding: 30px 0; }
	&__inner {
		padding-left: 80px;
		@include md {
			padding-left: 40px; }
		@include sm {
			padding-left: 0; } }
	.title-sm {
		margin-bottom: 40px;
		@include md {
			margin-bottom: 20px; } }
	.title-md {
		margin-bottom: 50px;
		@include md {
			margin-bottom: 25px; } } }
.home-prods {
	padding-top: 34px;
	padding-bottom: 34px;
	.title {
		strong {
			@include m {
				display: block; } } }

	&__inner {
		font-size: 0;
		max-width: 975px;
		margin-left: auto;
		margin-right: auto;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between; }
	.article {
		margin-bottom: 48px;
		max-width: 920px;
		margin-left: auto;
		margin-right: auto;
		@include md {
			margin-bottom: 20px; } }
	.title {
		margin-bottom: 80px;
		@include md {
			margin-bottom: 40px; } } }
.hprod {
	// margin-bottom: 30px
	display: inline-block;
	vertical-align: top;
	width: 420px;
	text-align: center;
	@include m {
		display: block;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 10px; }

	&__img {
		// width: 320px
		height: 312px;
		margin-bottom: 26px;
		position: relative;
		margin-left: auto;
		margin-right: auto;
		display: flex;
		align-items: flex-end;
		justify-content: center;
		&:before {
			content: '';
			position: absolute;
			bottom: -5px;
			width: 68px;
			height: 5px;
			background: $orange;
			left: 50%;
			transform: translateX(-50%); }
		img {
			max-width: 100%;
			// width: 100%
			// height: 100%
 } }			// object-fit: cover

	&__title {
		color: #010100;
		font-family: Roboto;
		font-size: 24px;
		font-weight: 700;
		margin-bottom: 8px;
		min-height: 60px;
		@include md {
			font-size: 20px;
			@include sm {
				font-size: 16px;
				min-height: auto; } } }
	a {
		display: inline-block;
		vertical-align: top;
		color: $black2;
		font-size: 24px;
		text-decoration: underline;
		margin-bottom: 15px;
		position: relative;
		@include md {
			font-size: 20px;
			@include sm {
				font-size: 16px; } }
		&:last-of-type {
			margin-bottom: 0; }
		&:hover {
			text-decoration: none; } } }
.advantages-sec {
	padding-top: 60px;
	padding-bottom: 88px;
	@include md {
		padding: 30px 0; }
	&__inner {
		padding-left: 36px;
		@include md {
			padding-left: 0; } }
	.title {
		margin-bottom: 69px;
		@include md {
			margin-bottom: 40px; } } }
.form-sec {
	padding-top: 25px;
	padding-bottom: 70px;
	@include md {
		padding-bottom: 25px; } }

.form-decor {
	border-left: 2px solid $white;
	border-right: 2px solid $white;
	border-bottom: 2px solid $white;

	padding: 32px 0px 44px;
	position: relative;
	max-width: 945px;
	margin-left: auto;
	margin-right: auto;
	@include xs {
		border: none; }
	.title-sm {
		@include xs {
			&:before, &:after {
				display: none; } } }
	p {
		font-size: 18px;
		text-align: center;
		color: $white;
		margin-bottom: 46px;
		@include md {
			padding: 0 10px;
			font-size: 16px; } }
	&__inner {
		padding: 0 20px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		@include m {
			display: block;
			text-align: center;
			padding: 0; } }
	.title-sm--line {
		position: absolute;
		top: 1px;
		transform: translateY(-50%);
		left: 0;
		width: 100%; } }
.why-we {
	background-color: #f8f8f8;
	padding-top: 66px;
	padding-bottom: 21px;
	margin-bottom: 63px;
	@include md {
		padding-top: 30px;
		margin-bottom: 30px; }
	&__inner {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;


		.why-we-item {
			flex-basis: 25%;
			@include md {
				flex-basis: 50%; }
			@include sm {
				flex-basis: 100%;
				p {
					max-width: 500px;
					margin-left: auto;
					margin-right: auto; } } } }
	.title {
		margin-bottom: 65px; } }
.why-we-item {
	margin-bottom: 50px;
	display: inline-block;
	vertical-align: top;
	padding: 0 5px;
	text-align: center;
	&__img {
		margin-bottom: 27px;
		img {
			max-width: 92px;
			max-height: 93px;
			height: auto; } }
	&__text {
		font-size: 18px; } }
.how-work {
	margin-bottom: 69px;
	@include md {
		margin-bottom: 30px; }
	&__inner {
		max-width: 945px;
		margin-left: auto;
		margin-right: auto; }

	.title {
		margin-bottom: 50px;
		@include md {
			margin-bottom: 25px; } }
	.decor-block {
		margin-bottom: 60px;
		@include md {
			margin-bottom: 25px; } }
	.video-block {
		margin-bottom: 50px;
		@include sm {
			margin-bottom: 30px; }
		&:last-of-type {
			margin-bottom: 0; } } }
.reviews {
	padding-top: 68px;
	padding-bottom: 75px;
	margin-bottom: 30px;
	@include md {
		padding: 30px 0; }
	&__inner {}

	.title {
		margin-bottom: 72px;
		@include md {
			margin-bottom: 40px; } } }

.r-slider {
	&__slide {}
	&__slide-inner {
		max-width: 945px;
		margin-left: auto;
		margin-right: auto;
		display: flex;
		@include md {
			max-width: 700px; }
		@include sm {
			display: block; } }
	&__photo {
		border: 1px solid $orange4;
		width: 264px;
		height: 264px;
		display: inline-block;
		vertical-align: top;
		flex-grow: 0;
		flex-shrink: 0;
		@include sm {
			margin-left: auto;
			margin-right: auto;
			display: block;
			margin-bottom: 10px; }
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } }
	&__right {
		display: inline-block;
		vertical-align: top;
		flex-grow: 1; }
	&__top, &__date, &__text {
		color: $white;
		font-weight: 300;
		padding-left: 28px;
		@include sm {
			padding-left: 0; } }
	&__top {
		border-bottom: 2px solid #fdb51a;
		padding-top: 5px;
		padding-bottom: 8px;
		font-size: 24px;
		margin-bottom: 8px;
		span {
			font-size: 18px; } }
	&__date {
		margin-bottom: 20px; }
	&__text {}
	.btn-prev {
		@include sm {
			left: -10px; } }
	.btn-next {
		@include sm {
			right: -10px; } } }
.pre-foot {
	margin-bottom: 16px;
	text-align: center;
	&__inner {
		width: 246px;
		margin-left: auto;
		margin-right: auto; }
	p {
		margin-bottom: 19px;
		font-size: 22px;
		@include sm {
			font-size: 18px; } }
	.link-orange {
		margin-bottom: 16px; }
	.btn {
		margin-bottom: 24px; }
	.btn, .socials {
		width: 225px; }
	.title {
		margin-bottom: 20px; } }
